/**
 * SEO component that queries for data with
 *  Gatsby's useStaticQuery React hook
 *
 * See: https://www.gatsbyjs.com/docs/use-static-query/
 */

import * as React from "react"
import PropTypes from "prop-types"
import { Helmet } from "react-helmet"
import { useStaticQuery, graphql } from "gatsby"

function SeoProduct({ description, lang, meta, title, logo }) {
  const { site } = useStaticQuery(
    graphql`
      query {
        site {
          siteMetadata {
            title
            url
            description
            author {
              name
              summary
            }
          }
        }
      }
    `
  )

  const metaDescription = description || site.siteMetadata.description
  const defaultTitle = site.siteMetadata?.title
  const metaLogo = site.siteMetadata.url + logo
  const author = site.siteMetadata?.author.name

  return (
    <Helmet
      htmlAttributes={{
        lang,
      }}
      title={title}
      titleTemplate={defaultTitle ? `%s | ${defaultTitle}` : null}
      meta={[
        {
          name: `description`,
          content: metaDescription,
        },
        {
          property: `og:title`,
          content: title,
        },
        {
          property: `og:description`,
          content: metaDescription,
        },
        {
          property: `og:type`,
          content: `article`,
        },
        {
          name: `twitter:card`,
          content: `summary`,
        },
        {
          property: 'og:image',
          content: metaLogo,
        },
        {
          name: 'twitter:image',
          content: metaLogo,
        },
        {
          name: `twitter:creator`,
          content: author || ``,
        },
        {
          name: `twitter:title`,
          content: title,
        },
        {
          name: `twitter:description`,
          content: metaDescription,
        },
      ].concat(meta)}
    >
      <script type="application/ld+json">{`
    "@context": "http://schema.org",
    "@type": "WebSite",
    "name": "${defaultTitle}",
    "url": "${site.siteMetadata.url}",
    "sameAs": [
      "https://www.yellowpages.com/etna-oh/mip/sunburst-pools-19572024",
      "https://www.facebook.com/Sunburst-Pools-166603570032111/"
    ]
  },
  {
    "@context": "https://schema.org/",
    "@type": "Product",
    "name": "${title}",
    "image": [
      "${metaLogo}"
     ],
    "description": "${description}",
    "brand": {
      "@type": "Brand",
      "name": "${defaultTitle}"
    }
  }
`}</script>
  </Helmet>
    
  )
}

SeoProduct.defaultProps = {
  lang: `en`,
  meta: [],
  description: ``,
}

SeoProduct.propTypes = {
  description: PropTypes.string,
  lang: PropTypes.string,
  meta: PropTypes.arrayOf(PropTypes.object),
  title: PropTypes.string.isRequired,
}

export default SeoProduct
